import { useCallback, useState, useEffect } from 'react';
export const useArrowFocus = (size, containerRef, elementsInRow, initialState = 0) => {
    const [currentFocus, setCurrentFocus] = useState(initialState);
    useEffect(() => {
        if (initialState > -1) {
            setCurrentFocus(initialState);
        }
    }, [initialState]);
    const handleKeyDown = useCallback((evt) => {
        const { key, target } = evt;
        if (containerRef && containerRef.current) {
            const containerChilds = [...containerRef.current.childNodes];
            const isFocusedElementInContainer = containerChilds.includes(target);
            if (isFocusedElementInContainer) {
                switch (key) {
                    case 'ArrowRight':
                        evt.preventDefault();
                        setCurrentFocus(currentFocus === size - 1 ? 0 : currentFocus + 1);
                        break;
                    case 'ArrowLeft':
                        evt.preventDefault();
                        setCurrentFocus(currentFocus === 0 ? size - 1 : currentFocus - 1);
                        break;
                    case 'ArrowDown':
                        evt.preventDefault();
                        setCurrentFocus(currentFocus === size - elementsInRow ? 0 : currentFocus + elementsInRow);
                        break;
                    case 'ArrowUp':
                        evt.preventDefault();
                        setCurrentFocus(currentFocus === 0 ? size - elementsInRow : currentFocus - elementsInRow);
                        break;
                }
            }
        }
    }, [size, currentFocus, setCurrentFocus]);
    useEffect(() => {
        document.addEventListener('keydown', handleKeyDown);
        return () => {
            document.removeEventListener('keydown', handleKeyDown);
        };
    }, [handleKeyDown]);
    return [currentFocus, setCurrentFocus];
};
