import { createSlice } from '@reduxjs/toolkit';
import { TODAY_ID } from 'src/constants';
export const initialSelectedDate = {
    id: TODAY_ID,
    day: new Date().getDate(),
    month: new Date().getMonth(),
    year: new Date().getFullYear(),
};
const initialCalendarState = {
    isMonthsView: false,
    isYearsView: false,
    highlightedYears: [],
    selectedDate: initialSelectedDate,
    yearsBlock: {
        isBlockUp: false,
        isBlockDown: false,
    },
};
const calendarSlice = createSlice({
    name: 'calendar',
    initialState: initialCalendarState,
    reducers: {
        setIsMonthsView(state, action) {
            state.isMonthsView = action.payload;
        },
        setIsYearsView(state, action) {
            state.isYearsView = action.payload;
        },
        setHighlightedYears(state, action) {
            if (state.highlightedYears.length) {
                state.highlightedYears.length = 0;
            }
            state.highlightedYears.push(...action.payload);
        },
        setSelectedDate(state, action) {
            state.selectedDate = Object.assign({}, action.payload);
        },
        blockYearsListChanging(state, action) {
            state.yearsBlock = Object.assign({}, action.payload);
        },
    },
});
export const calendarActions = calendarSlice.actions;
export default calendarSlice.reducer;
