export const changeSelectedDateToDayName = (selectedDate) => {
    const { day, month, year } = selectedDate;
    const date = new Date(`${year}-${month < 10 ? `0${month + 1}` : month + 1}-${day < 10 ? `0${day}` : day}`);
    const dayName = date.toLocaleDateString('en-US', { weekday: 'long' });
    return dayName;
};
